"use client"

import { GetTicketsPayload } from "../../../widgets/TicketList/types"
import { useForm } from "antd/es/form/Form"
import { DatePicker, Form, Input } from "antd"
import { setParams } from "../utils"
import { useSearchParams } from "next/navigation"

export function FilterForm() {
  const p = useSearchParams()
  const [form] = useForm<GetTicketsPayload>()

  const handleChange = (_: any, changedValues: GetTicketsPayload) => {
    setParams(changedValues)
  }

  return (
    <Form
      form={form}
      onValuesChange={handleChange}
      initialValues={{
        userName: p.get("userName") || "",
        startDate: p.get("startDate") || "",
        endDate: p.get("endDate") || "",
      }}
    >
      <Input
        placeholder='ФИО'
        style={{ width: 120 }}
      />
      <DatePicker placeholder='Начало' />
      <DatePicker placeholder='Конец' />
    </Form>
  )
}
