"use client"

import { Button, Flex, Table, Tooltip } from "antd"
import React, { useEffect } from "react"
import { Ticket } from "../../../shared/entities/Ticket/Ticket"
import { getTicketList } from "../api"
import { useSearchParams } from "next/navigation"
import { AppTag } from "../../../shared/ui/AppTag/ui"
import { StatusEnum } from "../../../shared/entities/Ticket/StatusEnum"
import { Prove } from "../../../shared/entities/Prove/Prove"
import Image from "next/image"
import { CheckCircleOutlined, DeleteOutlined } from "@ant-design/icons"

interface TicketTableProps {
  data: Ticket[]
}

const columns = [
  { title: "Студент", dataIndex: "user.name", key: "userName" },
  { title: "По дату", dataIndex: "startDate", key: "startDate" },
  { title: "с даты", dataIndex: "endDate", key: "endDate" },
  {
    title: "Статус",
    dataIndex: "endDate",
    key: "endDate",
    renderItem: (text: string) => {
      return (
        <AppTag variant={text === StatusEnum.PENDING ? "default" : text === StatusEnum.APPROVED ? "success" : "danger"}>
          {text}
        </AppTag>
      )
    },
  },
  {
    title: "Причина",
    dataIndex: "endDate",
    key: "endDate",
    renderItem: (text: string) => {
      return <AppTag variant='info'>{text}</AppTag>
    },
  },
  {
    title: "Подтверждения",
    dataIndex: "prooves",
    key: "prooves",
    renderItem: (prooves: Prove[]) => {
      return (
        <>
          {prooves.map(item => {
            return (
              <Image
                style={{ width: 50, height: 50 }}
                key={item.id}
                src={item.path}
                alt='empty'
              />
            )
          })}
        </>
      )
    },
  },
  {
    title: "Действия",
    dataIndex: "actions",
    key: "actions",
    renderItem: ({ id }: Ticket) => {
      return (
        <Flex
          justify='center'
          gap={"12px"}
        >
          <Tooltip title='Быстрое подтверждение'>
            <Button
              onClick={() => console.log(`Approve ticket with id: ${id}`)}
              type='primary'
              size='small'
            >
              <CheckCircleOutlined />
            </Button>
          </Tooltip>
          <Tooltip title='Быстрое отклонение'>
            <Button
              onClick={() => console.log(`Approve ticket with id: ${id}`)}
              color='danger'
              type='primary'
              size='small'
            >
              <DeleteOutlined />
            </Button>
          </Tooltip>
        </Flex>
      )
    },
  },
]

export const TicketTable: React.FC<TicketTableProps> = ({ data }) => {
  const [dataSource, setDataSource] = React.useState<Ticket[]>(data)
  const [isLoading, setIsLoading] = React.useState(false)

  const searchParams = useSearchParams()

  const fetchTickets = async () => {
    setIsLoading(true)
    getTicketList({
      userName: searchParams.get("userName") || undefined,
      limit: searchParams.get("limit") ? Number(searchParams.get("limit")) : 10,
      offset: searchParams.get("offset") ? Number(searchParams.get("offset")) : 0,
    })
      .then(response => {
        setDataSource(response.data)
      })
      .catch(error => {
        console.error("Error fetching tickets:", error)
      })
      .finally(() => {
        setIsLoading(false)
      })

    useEffect(() => {
      fetchTickets()
    }, [searchParams])
  }
  return (
    <Table
      loading={isLoading}
      columns={columns}
      dataSource={dataSource}
      rowKey='id'
      pagination={false}
    />
  )
}
